<template>
  <div>
    <div id="app">
      <video id="video" style="width: 100vw" autoplay muted></video>
      <p class="font-semibold text-center p-2">Version 0.4</p>
      <button id="button" @click="test">Send</button>
      <div class="row justify-content-center" style="position:absolute;bottom:5vh;width:100%;">
        <div class="col-6">
            <button type="button" @click="startScan" class="btn btn-sm btn-style10 btn-block text-xl" >
                Scan
            </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import * as faceapi from "@vladmandic/face-api";
import autophp from '@/plugins/_autophp'
let sdb=new autophp();
 var port;
// listen for messages
window.addEventListener('message', function(event) {
    if (event.data == 'capturePort') {
        // capture port2 coming from the Dart side
        if (event.ports[0] != null) {
            // the port is ready for communication,
            // so you can use port.postMessage(message); wherever you want
            port = event.ports[0];
            // To listen to messages coming from the Dart side, set the onmessage event listener
            port.onmessage = function (event) {
                // event.data contains the message data coming from the Dart side
                console.log(event.data);
            };
        }
    }
}, false);
import axios from 'axios';
export default {
  data() {
    return {
      message: "Hello Vue!",
    };
  },
  methods: {
    test(){
        port.postMessage('test');
    },  
    startVideo() {
      let that = this;
      const video = document.getElementById("video");
      navigator.mediaDevices
        .getUserMedia({
          video: {
            facingMode: "user", //environment
            resizeMode: "true",
          },
        })
        .then(function (mediaStream) {
          video.srcObject = mediaStream;
          video.play();
        });
    },
    async startScan(videoEl) {
      let that = this;
      var maxDescriptorDistance = 0.6;
      var video = document.getElementById("video");
      videoEl = video;
      var canvas = faceapi.createCanvasFromMedia(video);
      canvas.style.position = "absolute;";
        // taruh base64 ke canvas
        // const img = await canvas.loadImage(req.body.file);
        const img = canvas; // dengan file
        // mendapatkan descriptor dari canvas
        // let descriptorImg = await faceapi.detectSingleFace(img).withFaceLandmarks().withFaceDescriptor();
        let descriptorImg = await faceapi.detectSingleFace(img,new faceapi.TinyFaceDetectorOptions({ inputSize: 320 })).withFaceLandmarks(true).withFaceDescriptor()

        let tensor;
        let wajah;
        if (!descriptorImg) {
            wajah = false;
        } else {
            const regionsToExtract = [
                new faceapi.Rect(descriptorImg.detection.box.x, descriptorImg.detection.box.y, descriptorImg.detection.box.width, descriptorImg.detection.box.height)
            ]
            let faceImages = await faceapi.extractFaces(img, regionsToExtract)
            if (faceImages.length == 0) {
                wajah = false;
            } else {
                faceImages.forEach(cnv => {
                    wajah = cnv.toDataURL();
                })
            }
            // mengconvert data face descriptor ke type Float32Array
            tensor = [
                new Float32Array(descriptorImg.descriptor)
            ]
        }

        if (wajah) {
            let data = await axios.get('./data-wajah.json');
            let datas=data.data
            // let datas = await readJsonWajah();
            // console.log(datas)
            if (datas.length > 0) {
                // CEK DUPLICATE
                const labeledFaceDescriptors = await Promise.all(
                    datas.map(async label => {
                        let faces = JSON.parse(label.tensor)
                        if (faces) {
                            if (faces.length > 0) {
                                const faceDescriptors = []
                                faces.forEach(e => {
                                    // console.log(Object.values(e))
                                    faceDescriptors.push(new Float32Array(Object.values(e))) // multiple tensor data
                                })
                                // console.log(faceDescriptors)
                                return new faceapi.LabeledFaceDescriptors(JSON.stringify({
                                    id_user: label.id_user
                                }), faceDescriptors)
                            }
                        }
                    })
                )
                
                const faceMatcher = new faceapi.FaceMatcher(labeledFaceDescriptors, maxDescriptorDistance)
                if (descriptorImg) {
                    const results = faceMatcher.findBestMatch(descriptorImg.descriptor)
                    if (results._label == "unknown") {
                        if (tensor[0]) {
                            port.postMessage('false');
                            console.log(JSON.stringify({
                                // tensor: tensor,
                                // wajah: wajah,
                                message: "wajah tidak dikenali",
                                status: false,
                                confidence: 100 - (results._distance * 100)
                            }));
                            return;
                        }
                    } else {
                        let user = JSON.parse(results.label);
                        port.postMessage(JSON.stringify({
                            message: "Wajah Mirip dengan id user = " + user.id_user,
                            status: true,
                            duplicate:true,
                            confidence: 100 - (results._distance * 100),
                            id_user: user.id_user
                        }));
                        console.log(JSON.stringify({
                            // tensor: tensor,
                            // wajah: wajah,
                            message: "Wajah Mirip dengan id user = " + user.id_user,
                            status: true,
                            duplicate:true,
                            confidence: 100 - (results._distance * 100),
                            id_user: user.id_user
                        }));
                        return;
                    }
                }
            } else {
                port.postMessage('false');
                console.log(JSON.stringify({
                    message: 'Tidak ada wajah di Database ',
                    status: false
                }));
                return;
            }
        } else {
            port.postMessage('false');
            console.log(JSON.stringify({
                message: 'Wajah tidak ditemukan',
                status: false
            }));
            return;
        }
    },
    async loadModel() {
      var MODEL_URL = "./model";
      await faceapi.loadTinyFaceDetectorModel(MODEL_URL);
      await faceapi.loadFaceRecognitionModel(MODEL_URL);
      await faceapi.loadFaceLandmarkTinyModel(MODEL_URL);
    //   await faceapi.loadFaceExpressionModel(MODEL_URL);
      console.log("loading model berhasil ...");
      sdb.loadingOff();
      sdb.alert('loading model berhasil ...','bg-blue-500');
    },
  },
  mounted() {
    sdb.loadingOn();
    this.startVideo();
    setTimeout(() => {
      this.loadModel();
    }, 3000);
  },
};
</script>